import React, { useEffect, useState } from 'react'
import './style.css'
import { useHistory } from 'react-router-dom' 

import { logostore } from '../../store/logoStore';
import { logopositionstore } from '../../store/logopositionStore';
import * as dultlogo from '../../assets/Clean Walk/Loading_Screen/LD_DULT_Logo.png'

import { useParams } from 'react-router-dom';
import { useAuth } from '../../store/authStore';
import { instancestore } from '../../store/InstanceStore';



const LoadingScreen = () => {

  const { logo, loadlogowithid } = logostore();
  const { logoposition, loadlogo_arrayposition_iid } = logopositionstore();
  const { instance, loadinstance } = instancestore();

  const history = useHistory();
  const [logoimage] = useState({})

  const { auth } = useAuth();
  
  let params = useParams();
  useEffect(() => {

    if (!instance) {
      loadinstance()
    }

    if (params.instanceId) {
      if (!logo) {
        loadlogowithid(params.instanceId)
      }
      if (!logoposition) {
        loadlogo_arrayposition_iid(params.instanceId)
      }
    }
    else {
      if (!logo) {
        loadlogowithid(37)
      }
      if (!logoposition) {
        loadlogo_arrayposition_iid(37)
      }
    }
 



  }, [])

  const logoimage_position = ((obj: any) => {
    console.log(">>>>>>>>>>>", obj);

    const selectcase = (number: any) => {
      if (number === 1)
        return "logostyle1";
      else
        if (number > 1 && number < 4)
          return "logostyle2";
        else
          if (number >= 4 && number <= 6)
            return "logostyle3";
          else
            if (number > 6)
              return "logostyle4";
    }
    let a = selectcase(obj.length)
    return obj.map((obj1: any) => {
      return <img className={a} src={obj1.i_logo} />
    })
  })

  const loadinstanceicon = (instanceid: any) => {
    let a = " ";
    (instance) && instance.map((obj: any) => {
      // console.log(">> title >> : ", instanceid, obj.id);

      if (String(instanceid) === String(obj.id)) {
        // console.log(">> obj.l_icon >> : ", obj.mascot);
        a = obj.mascot
      } else { null }
    })
    return a
  }

  return (
    <>

      <div onClick={() => {
        if (localStorage.getItem('isFirsttime') === null) {
       
          history.replace('/StartScreen')
        } else {
          history.replace('/TaskListScreen')
        }
      }}>
        <div className='loadbgwrapper'>

          <div className="row1">
            <div className="loadinnerrow">
              <img className="logostyle1" src={dultlogo} />
              <img className="logostyle1" src={loadinstanceicon(params.instanceId)} />
            </div>
          </div>

          <div className='row2'>
            <div className="innerrowtitle"><u>PARTNERS</u></div>
            <div className="loadinnerrow">
              {(logoposition) && logoposition?.section2?.map(logoimage_position)}
            </div>
          </div>

          <div className='row3'>
            <div className="loadinnerrow">
              <div className="poweredby_text">Game Partner | Powered by Media Cannibal.</div>
            </div>
          </div>

          <div className='row4'>
            <img className='loadbgBelow' src={logoimage.position === 10 && logoimage.i_logo} />
          </div>

        </div>
      </div>

    </>
  )
}

export default LoadingScreen
